#dropdown-tela-inicial {
    position: absolute;
    z-index: 8;
}

    #dropdown-tela-inicial ul {
        position: absolute;
        margin: 0;
        padding: 8px 0;
        background-color: #2B2B2B;
        border: solid 1px #767676;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
        list-style: none;
        display: block;
    }

        #dropdown-tela-inicial ul ul {
            left: 100%;
            top: 0;
            display: none;
        }

            #dropdown-tela-inicial ul ul ul {
                left: 100%;
                top: 0;
                display: none;
            }

    #dropdown-tela-inicial li {
        display: block;
        list-style: none;
        position: relative;
        white-space: nowrap;
        text-align: left;
        color: #FFF;
        cursor: default;
        z-index: 5;
        cursor: pointer;
    }

        #dropdown-tela-inicial li:hover > ul {
            display: block;
        }

    #dropdown-tela-inicial label {
        display: none;
    }

    #dropdown-tela-inicial a {
