.icones-menu-dropdown {
    width: 16px;
    height: 16px;
    display: inline-block;
    margin: 0 4px;
}

    .icones-menu-dropdown.loading {
        background: url("~Imagens/carregador4.gif");
    }

    .icones-menu-dropdown.imagem {
        background: url("~Imagens/icones-menu-dropdown.png");
    }

    .icones-menu-dropdown.clear {
        background-position: 0 0;
    }

    .icones-menu-dropdown.fixar {
        background-position: -16px 0;
    }

    .icones-menu-dropdown.next {
        background-position: -32px 0;
    }

    .icones-menu-dropdown.checked {
        background-position: -48px 0;
    }

    .icones-menu-dropdown.icone-pequeno {
        background-position: -64px 0;
    }

    .icones-menu-dropdown.icone-medio {
        background-position: -80px 0;
    }

    .icones-menu-dropdown.icone-largo {
        background-position: -96px 0;
    }

    .icones-menu-dropdown.icone-grande {
        background-position: -112px 0;
    }

