.msg-success,
.msg-danger,
.msg-info,
.msg-warning {
    padding: 5px;
    font-weight: bold;
    font-style: italic;
    color: #FFF;
    z-index: 2;
}

.msg-success,
tbody tr:hover td.msg-success {
    background-color: #8fbf46;
}

.msg-danger,
tbody tr:hover td.msg-danger {
    background-color: #f55454;
}

.msg-info,
tbody tr:hover td.msg-info {
    background-color: #3c7fb1;
}

.msg-warning,
tbody tr:hover td.msg-warning {
    background-color: #f8d73f;
    color: #333;
}
