.ui-tabs {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 40px 1fr;
    -webkit-overflow-scrolling: touch;
    overflow: auto;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}

    .ui-tabs.ui-widget-content {
        background: none;
        border: none;
    }

.ui-icon-box {
    position: absolute;
    float: left;
    padding: 9px 5px;
}

.ui-tabs .ui-tabs-nav {
    background: #0072c6;
    /*background:#0b558b;*/
    grid-column: 1;
    grid-row: 1;
    border: none;
}

    .ui-tabs .ui-tabs-nav .ui-tabs-anchor {
        padding: 10px;
    }

    .ui-tabs .ui-tabs-nav li.ui-tabs-active .ui-tabs-anchor {
        padding: 10px 10px 9px 28px;
    }

    .ui-tabs .ui-tabs-nav li {
        background: none;
        border: none;
    }

        .ui-tabs .ui-tabs-nav li a {
            color: #fff;
        }

        .ui-tabs .ui-tabs-nav li.ui-tabs-active {
            border: 0;
            border-bottom-width: 0;
            background: #FFF;
            margin-bottom: 0;
            padding-bottom: 0;
            z-index: 1;
        }

            .ui-tabs .ui-tabs-nav li.ui-tabs-active a {
                color: #000;
            }

    .ui-tabs .ui-tabs-nav .ui-state-hover {
        border: 0;
        border-bottom-width: 0;
        background: #fff;
    }

        .ui-tabs .ui-tabs-nav .ui-state-hover a {
            color: #000;
        }

.ui-state-default .ui-icon-close {
    display: none;
}

.ui-state-active .ui-icon-close {
    display: inline-block;
}

.ui-datepicker-trigger {
    margin-left: 3px;
    cursor: pointer;
}

