.grid {
    width: 100%;
    border-collapse: collapse;
}

    .grid thead tr {
        background: #bbb;
    }

    .grid thead th {
        border: 1px solid #ddd;
        padding: 4px;
        font-weight: bold;
    }

    .grid thead .head th {
        background: #FFF;
    }

    .grid thead .head th {
        color: #000;
        font-weight: bold;
        border: 0;
        padding: 4px 4px 3px 4px;
    }

    .grid tbody tr:nth-child(odd) {
        background-color: #EEE;
    }

    .grid tbody td {
        border: 1px solid #ddd;
        padding: 4px 4px 3px 4px;
        vertical-align: top;
    }

    .grid tbody tr:hover td.msg.erro {
        background-color: #700 !important;
    }

    .grid tbody tr:hover td {
        background-color: #ddd !important;
    }

    .grid tfoot tr td {
        border-top: solid 2px #333;
        font-weight: 700;
        padding: 6px 8px;
    }

    .grid th .label,
    .grid td .label {
        display: block;
        font-size: 11px;
    }

.nogrid td {
    border: none !important;
}

@media print {
    .grid td {
        padding: 4px 4px 1px 4px !important;
    }

    .grid tfoot td {
        border-top: solid 2px #000000;
        border-left: none;
        border-right: none;
        border-bottom: none;
    }
}