#modal_overlayBG {
    position: fixed;
    z-index: 1040;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,.75);
}

#modal_window {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: #e2e4e9;
  z-index: 1050;
  color: #000000;
  text-align: left;
  width: 90%;
  height: 90%;
  border-radius: .25rem;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  overflow: auto;
}

.modal_title_box {
    position: sticky;
    top: 0px;
    left: 0px;
    width: 100%;
    z-index: 2;
}

#modal_Content {
    height: 100%;
}

#modal_title {
    padding: 8px;
    border-bottom: 1px solid rgba(0,0,0,.125);
    background-color: white;
    display: block;
}

#modal_closeWindow.icon-close {
    position: absolute;
    right: 8px;
    top: 8px;
    cursor: pointer;
}

.modal.fade.in .lab-modal-body {
    bottom: 0;
    opacity: 1;
  }
  
  .lab-modal-body h1 {
    font-size: 4rem;
  }
  
  .lab-modal-body p {
    margin: 0 0 1.62rem 0;
    line-height: 1.62;
    font-weight: 300;
    font-size: 1.62rem;
    color: #666;
  }
  
  .lab-modal-body {
    position: relative;
    bottom: -250px;
    margin: 150px auto 0;
    padding: 40px;
    max-width: 60%;
    height: auto;
    background-color: rgb(248, 250, 247);
    border: 1px solid #BEBEBE;
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-out, bottom 0.3s ease-out;
    -moz-transition: opacity 0.3s ease-out, bottom 0.3s ease-out;
    -o-transition: opacity 0.3s ease-out, bottom 0.3s ease-out;
    transition: opacity 0.3s ease-out, bottom 0.3s ease-out;
  }
  
  .close {
    margin-top: -20px;
    margin-right: -20px;
    text-shadow: 0 1px 0 #ffffff;
  }
  
  .popup-button {
    margin-top: 70px;
  }
