.icon-close {
    cursor: pointer;
    width: 20px;
    height: 20px;
    opacity: 0.3;
    cursor: pointer;
    outline: none;
}

.icon-close > span {
    font-size: 0.7em;
    margin-left: 13px;
    padding: 0 5px;
    line-height: 1.57em;
}

.icon-close:hover {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 16px;
}

.icon-close:before,
.icon-close:after {
    position: absolute;
    left: 9px;
    content: ' ';
    height: 12px;
    width: 2px;
    background-color: #333;
    top: 4px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
}

.icon-close:before {
    transform: rotate(45deg);
}

.icon-close:after {
    transform: rotate(-45deg);
}

