.busca {
    display: none;
}

.buscar-icon {
    background: url(/Imagens/icones-buscar.png);
    width: 32px;
    height: 32px;
    cursor: pointer;
    margin: 10px auto;
    display: block;
}


.icones-box {
    margin: 10px auto;
    width: 100%;
    text-align: center;
    display: none;
}

.start1 {
    width: 120px;
    height: 120px;
}

.start1 a {
    width: 100%;
    height: 100%;
}

.start2 {
    width: 250px;
    height: 120px;
}

.start2 .substart {
    padding: 20px;
    font-size: 24px;
}

.start2 .substart div {
    padding: 10px 0 0 75px;
}

.videos li {
    display: inline-block;
    width: 196px;
    vertical-align: top;
    margin: 0 10px 0 0;
    cursor: pointer;
}

.videos .frame {
    display: block;
    height: 110px;
    background: #A00;
}

.videos .titulo {
    padding: 5px 0;
    font-weight: bold;
    display: block;
}

.videos li:hover .titulo {
    text-decoration: underline;
}

.voltar-ao-topo {
    position: fixed;
    bottom: 10px;
    right: 10px;
    z-index: 5;
    display: none;
}

.mensagem-transacao {
    font-family: monospace;
}

.mensagem-transacao.regua {
    color: #F00
}

.scrollable-menu {
    height: auto;
    max-height: 200px;
    overflow-x: hidden;
}

.divFoto_box-image img {
    max-width: 100%;
}

.resultado-busca-foto .resultado-busca-foto-item {
    width: 19%;
    display: inline-block;
    vertical-align: top;
    position: relative;
}

.resultado-busca-foto-item-badges {
    display: none;
}

.resultado-busca-foto-item:hover .resultado-busca-foto-item-badges {
    display: block;
    position: absolute;
    background: white;
    padding: 0.5em;
    z-index: 2;
}


