/* Impressï¿½o */
@media print {
    #botoes {
        margin: .1em.1em;
        display: none;
    }

    .moldura {
        border: none;
        margin-bottom: 10px;
    }

    .moldura.corpo {
        padding: 0;
        margin: 0;
        border: none;
    }

    .cabecalho {
        padding: 5px 0 !important;
    }

    .noprint {
        display: none;
    }

    .seta-aba {
        display: none;
    }
}

.option-cor {
    width: 15px;
    height: 15px;
    display: inline-block;
    cursor: pointer;
}

.option-cor:hover,
.option-cor.active {
    border: solid 2px #333;
}

.box-cor {
    display: inline-block;
    width: 20px;
    height: 20px;
}

.tela-inicial {
    background: rgb(2,76,130);
}

.data-container {
    overflow: auto;
    border: 1px solid #CCC;
    margin-top: 5px;
    padding: 3px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    border-radius: 6px;
    border-image: initial;
}

.data-cell {
    display: inline-block;
    margin: 2px;
    padding: 3px 5px;
    background: #F2F5F7;
    border: 1px solid #DDD;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    border-image: initial;
}

.data-cell.empty {
    background: #f1eebe;
    border: 1px solid #ccca9e;
}

.data-cell.empty:hover {
    background: #ddd9a7;
    border: 1px solid #a1a07f;
}

.data-cell:hover {
    background: #E0E0E0;
}

