.paginacao {
    width: 100%;
    background: #B7CFE9;
}

    .paginacao td {
        text-align: center;
    }

    .paginacao a {
        color: #000;
    }

    .paginacao .primeira {
        background: url("~Imagens/primeira.png") left 2px no-repeat;
        padding-left: 15px;
    }

    .paginacao .anterior {
        background: url("~Imagens/anterior.png") left 2px no-repeat;
        padding-left: 15px;
    }

    .paginacao .proxima {
        background: url("~Imagens/proxima.png") right 2px no-repeat;
        padding-right: 15px;
    }

    .paginacao .ultima {
        background: url("~Imagens/ultima.png") right 2px no-repeat;
        padding-right: 15px;
    }

table thead tr .header {
    background: url("~Imagens/sort_both.png") center right no-repeat;
    cursor: pointer;
}

table thead tr .headerSortUp {
    background: url("~Imagens/sort_asc.png") center right no-repeat;
}

table thead tr .headerSortDown {
    background: url("~Imagens/sort_desc.png") center right no-repeat;
}

table.tablesorter tbody tr.odd td {
    background-color: #EDF3FE;
}