#nav {
    position: relative;
    z-index: 8;
}

    #nav a {
        color: #FFF;
        text-decoration: none;
    }

.navbar-header {
    width: 100%;
    padding: 13px;
    display: none;
}

.navbar-toggle {
    display: block;
    background: url(/Imagens/menu-icones.png) 0 0;
    width: 24px;
    height: 24px;
    background-position: -168px 0;
    border: none;
}

