/* Telas maiores que 600px */
@media screen and (min-width: 600px) {
    .navbar-header {
        display: none;
    }

    #nav,
    #Extratos,
    #dir {
        display: block;
    }

        #nav ul {
            padding: 0;
            margin: 0;
            float: left;
        }

        #nav li {
            display: inline-block;
            list-style: none;
            position: relative;
            color: #FFF;
            cursor: default;
            z-index: 5;
        }

        #nav ul ul li {
            width: 275px;
        }

        #nav ul ul {
            position: absolute;
            left: 0;
            display: none;
            background: #2a3235;
        }

            #nav ul ul ul {
                left: 100%;
                top: 0;
            }

        #nav li:hover > ul {
            display: block;
        }

        #nav ul ul li:hover {
            background: #0069d9;
        }

        .sistema:hover,
        .sair:hover {
            background: #2a3235;
        }

        #nav ul ul ul li {
            cursor: pointer;
        }

    /*--------CSS NOTIFICAÃÃO-----------------------------*/

    .AlertaNotificacao {
        background: #fa0c01;
        color: #fff;
        width: 14px;
        height: 14px;
        line-height: 14px;
        vertical-align: middle;
        text-align: center;
        font-size: 9px;
        border-radius: 50%;
        position: relative;
        top: -20px;
        left: 15px;
    }

    #listaNotificacoes {
        max-height: 300px;
        /*max-width: 275px;*/
        width: 340px;
        overflow-y: scroll;
        overflow-x: hidden;
    }

        /*Scroll da lista de notificacoes*/
        #listaNotificacoes::-webkit-scrollbar-track {
            -webkit-box-shadow: inset 0 0 4px rgba(0,0,0,0.3);
            background-color: #F5F5F5;
        }

        #listaNotificacoes::-webkit-scrollbar {
            width: 4px;
            background-color: #F5F5F5;
        }

        #listaNotificacoes::-webkit-scrollbar-thumb {
            background-color: #000000;
        }
    /*fim scroll*/


    #TextoNotificacao div {
        display: block;
        width: 255px;
        background: white;
        margin: 10px;
        white-space: pre-wrap;
        word-wrap: break-word;
        color: black;
        padding-top: 5px;
        padding-bottom: 10px;
        padding-left: 5px;
        padding-right: 5px;
        line-height: 2px;
    }

    #PublicadoEm {
        text-align: left;
        font-size: 10px;
        padding-bottom: 5px;
    }

    #Mensagem {
        text-align: justify;
        padding-bottom: 20px;
    }

    /*link do texto*/
    #LinkNotificacao a {
        color: black;
        text-decoration: none;
        text-align: center;
        font-size: 12px;
    }

    .menu {
        position: relative;
        z-index: 8;
    }

        .menu a {
            padding: 0.75em;
            display: block;
            color: #fff;
            text-align: left;
        }

        .menu ul {
            -webkit-box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.3);
            -moz-box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.3);
            box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.3);
        }

    #nav label {
        display: none;
    }

    .sistema {
        text-align: center;
    }

    .sistema .caption-sistema {
        display: none;
    }

    .icone-menu {
        background: url(/Imagens/menu-icones.png) 0 0;
        width: 24px;
        height: 24px;
        display: block;
    }

    .icone-menu.icone-menu-consignado {
        background-position: 0 0;
    }
    
    .icone-menu.icone-menu-habilitacao {
        background-position: -72px 0;
    }
    
    .icone-menu.icone-menu-financas {
        background-position: -48px 0;
    }
    
    .icone-menu.icone-menu-renavam {
        background-position: -24px 0;
    }
    
    .icone-menu.icone-menu-fiscalizacao {
        background-position: -96px 0;
    }
    
    .icone-menu.icone-menu-estatistica {
        background-position: -120px 0;
    }
    
    .icone-menu.icone-menu-inicio {
        background-position: -144px 0;
    }

    .icone-menu.icone-menu-notificacao {
        background-position: -168px 0;
    }

    .icone-menu.icone-menu-desenvolvedor {
        background-position: -192px 0;
    }
}

/* Telas menores que 600px */
@media screen and (max-width: 600px) {
    .navbar-header {
        display: block;
    }

    #nav,
    #Extratos,
    #dir {
        display: none;
    }

        #nav a {
            color: #FFF;
            padding: 10px;
            display: inline-block;
        }

        #nav ul {
            width: 100%;
            list-style: none;
            padding: 0px;
            position: relative;
        }

    .nivel {
        display: block;
        -webkit-box-shadow: 0 3px 15px 3px rgba(0, 0, 0, 0.4);
        -moz-box-shadow: 0 3px 15px 3px rgba(0, 0, 0, 0.4);
        box-shadow: 0 3px 15px 3px rgba(0, 0, 0, 0.4);
    }

    #nav ul .nivel {
        background: #5B5B5B;
    }

    #nav ul ul .nivel {
        background: #4B4B4B;
        margin: 0 0 0 25px;
    }

    #nav ul ul ul .nivel {
        background: #373737;
        margin: 0 0 0 50px;
    }

    #nav ul ul ul ul .nivel {
        background: #212121;
        margin: 0 0 0 75px;
    }

    #nav ul .nivel:hover {
        background: #4B4B4B;
    }

    #nav ul ul .nivel:hover {
        background: #373737;
    }

    #nav ul ul ul .nivel:hover {
        background: #212121;
    }

    #nav ul ul ul ul .nivel:hover {
        background: #000000;
    }

    #nav li > ul {
        display: none;
        text-decoration: none;
    }

    #nav:hover > ul,
    #nav:focus > ul,
    #nav:active > ul,
    #nav li:hover > ul,
    #nav li:focus > ul,
    #nav li:active > ul {
        display: block;
    }

    #nav ul ul ul li {
        cursor: pointer;
    }

    #nav input.trigger {
        position: absolute;
        top: -9999px;
        left: -9999px;
    }

        #nav input.trigger:checked ~ ul {
            display: block !important;
        }

    #nav label {
        display: block;
        padding: 10px;
        margin: 0;
        cursor: pointer;
        color: #FFF;
    }

    #Notificacao a {
        display: block;
        padding: 10px;
        color: #FFF;
        text-decoration: none;
    }

    #Notificacao ul {
        padding: 0;
        margin: 0;
        float: left;
        list-style: none;
    }

    #Notificacao li {
        display: inline;
        list-style: none;
        position: relative;
        white-space: break-word;
        text-align: left;
        color: #FFF;
        cursor: default;
        z-index: 5;
    }

    #Notificacao ul ul {
        position: absolute;
        left: 0;
        display: none;
        background: #2a3235;
        top: 50px;
    }

        #Notificacao ul ul li {
            width: 240px;
        }

            #Notificacao ul ul li:hover {
                background: #212528;
            }

    #TextoNotificacao p {
        width: 220px;
        word-wrap: break-word;
        background: white;
        color: black;
        padding-top: 5px;
        padding-bottom: 10px;
        padding-left: 5px;
        line-height: 1.5;
        margin: 10px;
        text-align: justify;
    }

    #MenuNotificacoes {
        text-align: center;
        padding: 13px;
        width: 50px;
        height: 50px;
    }

    .AlertaNotificacao {
        background: #fa0c01;
        color: #fff;
        width: 12px;
        height: 12px;
        line-height: 12px;
        /*padding: 1px;*/
        vertical-align: middle;
        text-align: center;
        font-size: 8px;
        border-radius: 50%;
        /*-moz-border-radius:50%;
		-webkit-border-radius:50%;*/
        position: relative;
        top: -20px;
        left: 18px;
    }
}

@media print {
    * {
        color: #000;
    }
}
