.metro-layout .content .items {
    position: relative;
    overflow: hidden;
}

.horizontal .content .items {
    height: 100%;
}

.metro-layout .items .col {
    display: inline-block;
    vertical-align: top;
    margin-right: 20px;
    padding-left: 0;
    padding-right: 0;
}

.items .col1 {
    max-width: 110px;
}

.items .col2 {
    max-width: 220px;
}

.items .col3 {
    max-width: 312px;
}

.items .col4 {
    max-width: 416px;
}

.metro-layout .box {
    float: left;
    position: relative;
    text-decoration: none;
    cursor: pointer;
    color: #fff;
    z-index: 1;
    margin: 2px;
    text-align: center;
}

.metro-layout .icon {
    background: url("~Imagens/icones-tela-inicial.png");
    display: inline-block;
}

.metro-layout .pequeno .icon {
    width: 24px;
    height: 24px;
    padding: 8px;
}

.metro-layout .medio .icon {
    width: 48px;
    height: 48px;
    margin: 10px auto;
}

.metro-layout .largo .icon, .metro-layout .grande .icon {
    width: 48px;
    height: 48px;
    margin: 10px auto;
}

.items .caption {
    position: absolute;
    left: 5px;
    bottom: 5px;
    text-align: left;
}

.items .caption-modulo {
    font-size: 9px;
    color: #eee;
    line-height: 8px;
}

.items .caption-pagina {
    font-weight: bold;
}

.items .caption-noticia {
    position: absolute;
    left: 0;
    bottom: 0;
    text-align: left;
    padding: 10px;
    width: 100%;
}

.items .borda-interna {
    border: solid 3px;
    padding: 3px;
}

    .items .borda-interna .caption {
        left: 3px;
        bottom: 3px;
    }

.items .noticia {
    background-position: top center;
    background: url(/Imagens/logo-detran.png);
    display: block;
    background-size: 130px;
    background-repeat: no-repeat;
    background-position: center 10px;
}

.items .background-noticia {
    width: 100%;
    height: 100%;
    display: block;
    background-size: 205px auto;
    background-repeat: no-repeat;
}

.icone-noticia {
    background: url("~Imagens/icone-noticia.png");
    width: 24px;
    height: 24px;
    display: block;
    float: left;
    margin-top: 2px;
}

.items2 {
    list-style-type: none;
    padding: 0;
    margin-bottom: 21px;
}

    .items2 a {
        display: block;
        margin: 0;
        padding: 3px;
        float: left;
        width: 20%;
    }

        .items2 a img {
            padding: 5px;
            margin-right: 8px;
            float: left;
        }

    .items2 .caption {
        padding-top: 8px;
    }

    .items2 .caption-modulo {
        font-weight: bold;
        font-style: italic;
        font-size: 1.2em;
        margin-bottom: 5px;
    }

    .items2 a.borda-interna {
        border: solid 3px;
        margin: 0;
        padding: 0;
    }

.metro-layout .pequeno {
    width: 48px;
    height: 48px;
}

.metro-layout .medio {
    width: 100px;
    height: 100px;
}

.metro-layout .largo {
    width: 204px;
